import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";
import { getCategories } from "@modules/cms";
import styles from "./index.module.scss";
import { connect } from "react-redux";

const mapStateToProps = (state: any) => ({
    cms: state.cms
})

const mapActionsToProps = {
    getCategories: getCategories
}

const Categories: React.FunctionComponent<any> = (props) => {
    useEffect(() => {
        if (props?.cms?.categories?.length === 0) {
            props.getCategories({}).then((response: any) => {

            }).catch((error: any) => {

            })
        }
    }, [])
    return (
        <>
            <div className={styles.categories}>
                {props?.showTitle && <h1 className={styles.title} role="h6">Kategoriler</h1>}
                <div className={classNames("container", styles.container)}>
                    <div className={styles.categoriesContainer}>
                        {(props?.cms?.categories || []).map((item, index) => {
                            return (
                                <Link className={styles.category} to={"/yazilarim/" + item?.attributes?.path}>
                                    <div className={styles.cover}>
                                        <img src={item?.attributes?.coverImage?.data?.attributes?.url} />
                                    </div>
                                    <div className={styles.title}>
                                        {item?.attributes?.title}
                                    </div>
                                </Link>

                            )
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(Categories);